*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
/*   border: 1px solid black; */
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(11, 63, 76);
}