.App-logo {
  position: fixed;
  top: 1%;
  left: 1%;
  width: 7vw;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#calculator {
  width: 30vw;
  aspect-ratio: 1/1.5;
  display: flex;
  flex-direction: column;
  background-color: rgb(24, 24, 24);
}

#displayFormula {
  font-size: 3.4rem;
  color: beige;
  width: 100%;
  height: 20%;
  padding: 0 2rem 0 2rem;
  display: flex;
  align-items: center;
  justify-content: end;
}

#display {
  width: 10vw;
  height: 10vh;
  background-color: aqua;
  position: absolute;
}

.buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr; 
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: 
    "ac ac . ."
    ". . . ."
    ". . . ."
    ". . . equal"
    "zero zero . equal"; 
  width: 100%;
  height: 80%;
  gap: 0.2rem;
  padding: 1rem;
}

button {
  font-family: 'Montserrat';
  font-size: 2.4rem;
  font-weight: 500;
  color: rgb(26, 46, 61);
  background-color: rgba(195, 255, 254, 0.742);
  border-radius: 0.2rem;
  border: none;
}

button:hover {
  background-color: rgba(174, 222, 221, 0.742);
}

button:active {
  background-color: rgba(179, 231, 230, 0.742);
}

#clear {
  grid-area: ac;
  background-color: rgba(255, 212, 195, 0.742);
}

#clear:hover {
  background-color: rgba(222, 186, 174, 0.742);
}

#clear:active {
  background-color: rgba(231, 192, 179, 0.742);
}


#zero {
  grid-area: zero;
}

#equals {
  grid-area: equal;
  background-color: rgba(195, 243, 255, 0.742);
}

#equals:hover {
  background-color: rgba(174, 204, 222, 0.742);
}

#equals:active {
  background-color: rgba(179, 215, 231, 0.742);
}

@media (max-width: 71.25em) {
  #calculator {
    width: 35vw;
  }
}

@media (max-width: 62.5em) {
  #calculator {
    width: 40vw;
    aspect-ratio: 1/1.4;
  }
  button {
    font-size: 2.2rem;
  }
}

@media (max-width: 54em) {
  .App-logo {
    width: 10vw;
  }

  #displayFormula {
    font-size: 3.2rem;
  }

  #calculator {
    width: 50vw;
  }
}

@media (max-width: 46.5em) {
  #calculator {
    width: 55vw;
  }
}

@media (max-width: 39em) {
  .App-logo {
    width: 15vw;
  }
  #calculator {
    width: 65vw;
  }
}

@media (max-width: 34em) {
  #calculator {
    width: 75vw;
  }
}

@media (max-width: 27.5em) {
  #calculator {
    width: 85vw;
  }
}

@media (max-width: 22.75em) {
  .App-logo {
    width: 20vw;
  }
  #calculator {
    width: 100vw;
    aspect-ratio: 1/1.7;
  }

  #displayFormula {
    font-size: 3rem;
  }

  .buttons {
    padding: 1.5rem;
  }
}

@media (max-width: 19.75em) {
  #calculator {
    width: 100vw;
    aspect-ratio: 1/1.8;
  }
  .buttons {
    padding: 1rem;
  }
}
